    import headTop from '@/components/base/header/head';
    export default {
        components: {
            headTop
        },
        data() {
            return {
                allmoney: 0,
                value1: null,  //  选择时间
                tipList: [],
            }
        },
        mounted(){
            this.value1 = this.common.commonFun.get_date(1);
            this.getInfo();
        },
        methods:{
            getInfo(){
                this.common.commonFun.ajax({
                    dom: this,
                    url: this.common.apiUrl.reward,
                    type: "post",
                    data: {
                        start: this.value1,
                        end: this.value1,
                    },
                    success: (response, allResponse) => {
                        console.log('response',response)
                        this.tipList = response.list;
                        this.allmoney = response.allmoney
                        // this.tipList = this.tipList.concat(response.list);
                    }
                });
            },
//            选择时间按钮弹窗
            onConfirm () {
                console.log("我点击了确定按钮",this.value1)
                this.tipList = [];
                this.getInfo();
            },
        }
    }



